import React from 'react'
import {Aboutcomp,AboutComp} from '../component/Aboutcomp'
import about from '../css/about.css'
import Footer from './Footer'
import Navbar from './Navbar'
const Aboutus = () => {
    return (
        <>
        <Navbar/>
        <div className="bodys" style={{backgroundColor:'#24272c'}}>
      <div className="headers-section " style={{maxWidth: '850px', margin: '8% auto', color: 'black'}}>
        <div className="dark-body-bg">
            <div className="containers-fluid">
                <section id="about-us-container" >
                    <h2 id="about-page-header" >
                        <span id="about-us-gradient">Learntothrive:</span>
                        an training institute deisgned to bring out best in you.
                    </h2>
                    <h4 id="about-page-subheader" >In a world where learning only comes from our
                         formative years in school and college,Learntothrive is the new, easy, and
                          definitive path for keeping up with the 21st century. It is the platform
                           where you can finally stop trying to fit in a system crafted for the masses
                            and learn in a unique way that brings out the best version.
                    </h4>
                    <div className="rows ">
                        <div className="wrapper">
                            <Aboutcomp image={'data:image/svg+xml;base64,'} 
                            title={'500+'} sub={'Internship Patners'} colour={'#fbc93b'}/>

                            <Aboutcomp image={'data:image/svg+xml;base64,'}
                             title={'100,000+'} sub={'Active Learners'} colour={'#00e6ff'}/>

                             <Aboutcomp image={'data:image/svg+xml;base64,'}
                              title={'100+'} sub={'Industry Experts'} colour={'#ff8a00'}/>
                            
                        </div>
                        <div className="about-page-definition">
                            <div className="logo">
                                <img src="data:image/png;base64," alt="" style={{margin: '6% auto 0% auto', width: '100%', maxWidth: '220px', justifySelf: 'stretch'}} className="col-6 mb-0"></img>
                            </div>
                            <div className="page-definition">
                               
                                <p style={{alignSelf: 'center',marginTop: '4em', maxWidth: '450px', color: 'hsla(0,0%,100%,.9)', fontWeight: 'lighter'}}>The inspired act of venturing beyond conventional ways of learn. An education system crafted for each individual’s specific needs.</p>
                            </div>
                        </div>
                        <div className="about-page-vision-container">
                            
                            <AboutComp image={'data:image/png;base64,'} 
                            title={'VISION'} sub={'To provide alternative learning & teaching methods to enable the youth towards employability.'}/>

                            <AboutComp image={'data:image/png;base64,'} 
                            title={'MISSION'} sub={'To create a powerful online learning ecosystem for everyone, tailored for each one.'}/>
                        </div>
                    </div>
                    <div className="about-page-who-are-we" style={{marginTop: '6em', marginLeft: '1em', marginRight: '1em'}}> 
                        <div className="rows-who">
                            <div  style={{justifySelf: 'center'}}>
                                <h4 style={{color: 'white', fontSize: '3em', fontWeight: 'bolder'}}>Who are we?</h4>
                                <p style={{fontWeight: '1.5em', lineHeight: '2.5', marginTop: '0%', color: 'hsla(0,0%,100%,.9)'}}>We’re witnessing a generation that asks questions, has opinions and takes initiative to make the world a better place.
                                    As Unschoolers, we’re<span style={{color: '#ffbc00', fontStyle: 'italic'}}> catalysts of training for this generation.</span></p>
                            </div>
                            <div >
                                <div className="who-image-container">
                                    <img src="https://www.unschool.in/static/group-2210-908b736f311ddcbda8bf191e3c6c2a43.png" width="350" height="450" alt=""></img>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
       </div>
       </div>
        </div>
        <Footer/>
        </>
    )
}

export default Aboutus
